<template>
  <div>
    <div style="position: relative">
      <img v-if="is_data_fetched" v-bind:src="answer_url" />
      <div v-if="!is_data_fetched" :style="image_url" class="image" />
    </div>
    <p>
      Задайте вопрос, на который можно ответить «да» или «нет»:
      <input v-model="question" />
    </p>
    <p>{{ answer }}</p>
  </div>
</template>


<script>
import axios from "axios";
export default {
  data() {
    return {
      is_data_fetched: false,
      updated: true,
      question: "",
      answer: "Пока вы не зададите вопрос, я не могу ответить!",
      answer_url: require("@/assets/question.png"),
      image_url: {
        height: "50vh",
        backgroundImage: `url(${require("@/assets/question.png")})`,
        position: "",
      },
    };
  },
  watch: {
    // эта функция запускается при любом изменении вопроса
    // newQuestion, oldQuestion
    question: function () {
      this.answer = "Ожидаю, когда вы закончите печатать...";
      this.answer_url = require("@/assets/question.png");
      this.is_data_fetched = false;
      this.image_url.position = "";
      this.getAnswer();
    },
  },
  methods: {
    setColor: () => {
      return "red";
    },
    getAnswer: function () {
      if (!this.question.length) {
        return;
      }
      if (this.question.indexOf("?") === -1) {
        this.answer = "Вопросы обычно заканчиваются вопросительным знаком. ;-)";
        return;
      }
      this.answer = "Думаю...";
      var vm = this;
      axios
        .get("https://yesno.wtf/api")
        .then(function (response) {
          // vm.answer = _.capitalize(response.data.answer);
          vm.answer_url = response.data.image;
          vm.answer = "Лови ;)";
          vm.image_url.backgroundImage = `url(${require("@/assets/question.png")})`;
          vm.image_url.height = "50vh";
          vm.image_url.position = "absolute";
          vm.is_data_fetched = true;
        })
        .catch(function (error) {
          vm.answer = "Упс, что-то пошло не так... " + error;
        });
    },
  },
};
</script>

<style scoped>
.image {
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  top: 0;
  width: 100%;
  /* clip-path: circle(150px at center); */

  animation: fadein ease-in 1;
  animation-duration: 0.5s;
  animation-fill-mode: forwards;
}
img {
  height: 50vh;
  mask-image: url("../assets/question.png");
  mask-clip: content-box;
  mask-position: center;
  mask-size: contain;
  mask-repeat: no-repeat;
  animation: fadein ease-in 1;
  animation-duration: 0.5s;
  animation-fill-mode: forwards;
}
@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
</style>